import { mapGetters } from 'vuex'
import { mapState } from 'vuex'
export default {
    data() {
        return {
            tagList:[],
            dataList: [],
            noData: false,
            pageFlag:'',
            preRoute:this.$route.query.info,
            mySwiper:{},
            info: {},
            num: "",
            spinner1: 0,
            pdfShow: false,
            videoShow: false,
            isOne: false,
            isTwo: false,
            pdfFlag: false,
            videoFlag: false,
            btnFlag: false,
            content: ""
        }
    },
    created:function () {
        this.pageFlag = this.$route.query.pageFlag;
    },
    methods: {
        buyClose:function(){
            $("#section1").show();
            $("#section2").hide();
        },
        buyCreate: function() {
            $("#section1").hide();
            $("#section2").show();
            this.$store.dispatch("showLoading");
            var ssrBase = "https://bird.ioliu.cn/v1/?url=";
            let _this = this;
            if (this.info.detail) {
                $.get(ssrBase + this.info.detail, function(res) {
                    _this.content = res;
                    _this.$store.dispatch("hideLoading");
                });
            } else {
                this.$store.dispatch("hideLoading");
            }
        },
        changeTab:function (id) {
            let _this = this
            let params = {
                hotelid: localStorage.HOTELID,
                lang: localStorage.LANGUAGE,
                limit:0,
                tagid:id,
                status:1
            }

            this.$store.dispatch('getShoppingList', params).then(function (res) {
                $(".g-scrollview").scrollTop(0);
                _this.dataList=res.data.data.list;
                if(_this.dataList.length==0){
                    _this.noData = true;
                }else{
                    _this.noData = false;
                }
            });
        },
        goDetail:function (id) {
            let data = {};
            for(var key in this.dataList){
                if (this.dataList[key].id == id){
                    data = this.dataList[key];
                    break;
                }
            }
            this.info = data;
            this.buyCreate();

        },
        goBack:function(){
            if(this.preRoute){
                this.$router.push('/'+this.preRoute);
            }else{
                this.$router.push('/home');
            }
        },
        apply: function() {
            let alobj = new alertLanguage();
            let obj = alobj.getAlertMsg(localStorage.LANGUAGE);
            let title = obj.title;
            let msg = obj.shopping.msg;
            let sureBnt = obj.sureBnt;
            let cancelBnt = obj.cancelBnt;

            let _this = this;
            let dialog = window.YDUI.dialog;
            dialog.confirm(title,msg, [
                {
                    txt: sureBnt,
                    color: false,
                    callback: function () {
                        _this.toBuy();
                    }
                },
                {
                    txt: cancelBnt,
                    color: false,
                    callback: function () {

                    }
                }
            ]);
        },
        toBuy: function(){
            $(".buy_foot button").attr("disabled",true);
            $(".buy_foot button").css({background: "grey"});
            let _this = this;
            let params = {
                hotelid: localStorage.HOTELID,
                shoppingid: this.info.id,
                token: localStorage.TOKEN,
                count: this.spinner1
            };
            this.$store.dispatch("getShoppingOrder", params).then(res => {
                if (res.data.code == 0) {
                    alert(this.language.msg.buy_info);
                } else {
                    this.$dialog.toast({ mes: res.data.msg, timeout: 1000 });
                }
                $(".buy_foot button").attr("disabled",false);
                $(".buy_foot button").css({background: "#f0c366"});
            });
        },
        toPDF: function() {
            if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
                openFile(this.detail.pdf);
            } else {
                openPdf(this.detail.pdf);
            }
        },
        toVideo: function() {
            if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
                openFile(this.detail.video);
            } else {
                openVideo(this.detail.video);
            }
        }
    },
    mounted:function () {
        let _this = this
        let params = {
            hotelid: localStorage.HOTELID,
            lang: localStorage.LANGUAGE,
            limit:0
        }
        this.$store.dispatch('getShoppingTagList', params).then(function (res) {
            _this.tagList = res.data.data.list;
            //初始化tab标签
            $(function() {
                //初始化tab选择项
                _this.mySwiper = new Swiper(".raiders .swiper-container", {
                    pagination: ".swiper-pagination",
                    slidesPerView: 3,
                    paginationClickable: true,
                    spaceBetween: 0,
                    initialSlide: 0
                });
                //添加标签点击样式
                $(".ra").on("click", ".swiper-slide", function () {
                    $(this)
                        .addClass("active")
                        .siblings()
                        .removeClass("active");
                });
                //tab的click事件触发选择的初始化内容
                $(".swiper-slide:eq(0)").click();
            });
        });

        //一级页面falg
        isHomePage(0)
    },
    components: {
    },
    computed: {
        ...mapState({
            language: state => state.language.language
        })
    },
};



